.App {
  display: -ms-flexbox;
  display: flex;
  min-height: 100vh;
  -ms-flex-direction: column;
      flex-direction: column;
  font-family: sans-serif;
}


.App-header {
  height: 80px;
  padding: 20px;
  color: white;
  text-align: center;
}

.App-title {
  font-size: 2.5em;
  color: black;
  font-family: "Lucida Sans Unicode", "Lucida Grande", sans-serif;
}

.App-intro {
  font-size: large;
}

.main {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
  margin-bottom: 30px;
  margin-top: 25px;
}

.left {
  -ms-flex: 60% 1;
      flex: 60% 1;
  height: 100%;
}

.right {
  -ms-flex: 40% 1;
      flex: 40% 1;
  height: 100%;
}

#bilde {
  background-color: lightgrey;
  height: 500px;
}

#tekst {
  background-color: lavender;
  height: 350px;
}

#lyd {
  background-color: white;
  height: 150px;
}

.kategori {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
  -ms-flex-pack: distribute;
      justify-content: space-around;
}

.kategori > div {
  margin: 20px 0px 20px 0px;
  width: 350px;
}

.radioButtons{
  background-color: #ebeef9;
  padding:20px;
}

.choiceName {
  margin: 0;
  background-color: #B7C0E9;
  padding:20px;
}



#faner {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
  height: 50px;
}

.btn {
  width: 25%;
  height: 100%;
  text-decoration: none;
  background-color: #B7C0E9;
  border: none;
  font-size: 16px;
}

.btn:hover {
  background-color: #d8ddf3;
}

.btn:focus {
  background-color: #d8ddf3;
  outline: none;
}


h3 {
  text-align: center;
}

input {
  margin-left: 30%;
  margin-bottom: 20px;
}

.welcomeText {
  width: 70%;
  margin: 0 auto;
  padding-top: 50px;
  padding-bottom: 50px;
  line-height: 1.5em;
}

.stage{
  margin-left: 40px;
  margin-top: 50px;
}

.content-wrapper{
  -ms-flex: 1 0 auto;
      flex: 1 0 auto;
}


.wrapper {
  -ms-flex: 1 1;
      flex: 1 1;
}

.footer {
  background-color: #222;
  height: 50px;
  text-align: center;
  width: 100%;
}

.footer > p {
  color: #888;
  font-size: 14px;
}

@media screen and (max-width: 1100px) {
  .kategori > div{
    width: 220px;
  }
}

@media screen and (max-width: 700px) {
  .main {
    -ms-flex-direction: column;
        flex-direction: column;
  }

  .kategori{
    -ms-flex-direction: column;
        flex-direction: column;
  }

  .kategori > div {
    width: 300px;
    margin: 0 auto;
    margin-top: 10px;
    margin-bottom: 10px;
  }
}



